
body,
html {
  margin: 0;
  padding: 0;
  font: 300 1em/1.8 PingFang SC, Lantinghei SC, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans-serif;
}
canvas {
  border: 1px solid #ddd;
}
ol .active {
  color: red;
}
h5 {
  text-align: center;
}
.result {
  margin: 0;
  padding: 0;
  list-style: none;
}
.result li {
  position: relative;
  height: 30px;
  line-height: 30px;
  display: inline-block;
}
.result li img {
  height: 100%;
}
.result li:hover {
  background-color: #ddd;
}
.result li:hover .preview {
  display: block;
  position: absolute;
  top: 0;
  right: -200px;
  height: 200px;
}
.result li .preview {
  display: none;
}



